import {isPresent} from '../facade/lang';
import {StringMapWrapper} from '../facade/collection';
import {AUTO_STYLE} from './metadata';

export class AnimationStyleUtil {
  static balanceStyles(previousStyles: {[key: string]: string|number},
                       newStyles: {[key: string]: string|number}): {[key: string]: string|number} {
    var finalStyles: {[key: string]: string|number} = {};
    StringMapWrapper.forEach(newStyles, (value, prop) => {
      finalStyles[prop] = value;
    });

    StringMapWrapper.forEach(previousStyles, (value, prop) => {
      if (!isPresent(finalStyles[prop])) {
        finalStyles[prop] = null;
      }
    });

    return finalStyles;
  }

  static clearStyles(styles: {[key: string]: string|number}): {[key: string]: string|number} {
    var finalStyles: {[key: string]: string|number} = {};
    StringMapWrapper.keys(styles).forEach(key => {
      finalStyles[key] = null;
    });
    return finalStyles;
  }

  static stripAutoStyles(styles: {[key: string]: string|number}): {[key: string]: string|number} {
    var finalStyles: {[key: string]: string|number} = {};
    StringMapWrapper.forEach(styles, (value, key) => {
      if (value != AUTO_STYLE) {
        finalStyles[key] = value;
      }
    });
    return finalStyles;
  }

  constructor(public styles: {[key: string]: {[key: string]: string|number}}) {}

  lookup(value: any): {[key: string]: string|number} {
    var data = this.styles[<string>value];
    if (!isPresent(data)) {
      data = this.styles['void'];
    }
    return data;
  }
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
